export const dynamic = "force-dynamic";

import NaverMap from "@/components/NaverMap";
import { auth } from "@/lib/auth";
import { prisma } from "@/lib/db";
import type { Listing as MockListing } from "@/lib/mockData";
import { listings as MOCK } from "@/lib/mockData";
import Image from "next/image";
import Link from "next/link";
import { notFound } from "next/navigation";

type VM = {
  id: number;
  title: string;
  category: string;
  address_area: string | null;
  rent_monthly: number | null;
  deposit: number | null;
  goodwill_price: number | null;
  monthly_sales: number | null;
  parking_spaces: number | null;
  lat: number | null;
  lng: number | null;
  ownerId: string | null;
  createdAt: string;
  photos: string[];
};

export default async function ListingDetailPage({
  params,
}: {
  params: Promise<{ id: string }>;
}) {
  const { id } = await params;
  const num = Number(id);
  if (!Number.isFinite(num)) notFound();

  // 1) DB(존재하는 컬럼만)
  const db = await prisma.listing.findUnique({
    where: { id: num },
    select: {
      id: true,
      title: true,
      category: true,
      address_area: true,
      rent_monthly: true,
      deposit: true,
      goodwill_price: true,
      monthly_sales: true,
      parking_spaces: true,
      lat: true,
      lng: true,
      ownerId: true,
      createdAt: true,
    },
  });

  // 2) mock(사진/썸네일/좌표 보강)
  const mock: MockListing | undefined = (MOCK as MockListing[]).find(
    (x) => x.id === num
  );
  type MockMedia = NonNullable<MockListing["media"]>[number];

  const fromMockPhotos: string[] =
    mock?.media
      ?.filter((m: MockMedia) => m.file_type === "photo" && !!m.file_url)
      ?.map((m: MockMedia) => m.file_url) ?? [];
  if (mock?.thumbnail_url) fromMockPhotos.unshift(mock.thumbnail_url);

  // 3) VM 병합
  const vm: VM | null = db
    ? {
        id: db.id,
        title: db.title ?? "",
        category: db.category ?? "",
        address_area: db.address_area ?? "",
        rent_monthly: db.rent_monthly ?? null,
        deposit: db.deposit ?? null,
        goodwill_price: db.goodwill_price ?? null,
        monthly_sales: db.monthly_sales ?? null,
        parking_spaces: db.parking_spaces ?? null,
        lat: db.lat ?? (mock?.lat ?? null),
        lng: db.lng ?? (mock?.lng ?? null),
        ownerId: db.ownerId ?? null,
        createdAt: db.createdAt.toISOString(),
        photos: fromMockPhotos, // 사진은 mock만 사용
      }
    : mock
    ? {
        id: mock.id,
        title: mock.title,
        category: mock.category,
        address_area: mock.address_area ?? "",
        rent_monthly: mock.rent_monthly ?? null,
        deposit: mock.deposit ?? null,
        goodwill_price: mock.goodwill_price ?? null,
        monthly_sales: mock.monthly_sales ?? null,
        parking_spaces: mock.parking_spaces ?? null,
        lat: mock.lat ?? null,
        lng: mock.lng ?? null,
        ownerId: null,
        createdAt: mock.created_at,
        photos: fromMockPhotos,
      }
    : null;

  if (!vm) notFound();

  // 권한
  const session = await auth();
  const meId = (session?.user as any)?.id as string | undefined;
  const myRole = (session?.user as any)?.role as "ADMIN" | "CLIENT" | undefined;
  const canEdit = !!meId && (vm.ownerId === meId || myRole === "ADMIN");

  const fmt = (n?: number | null) =>
    n == null ? "-" : new Intl.NumberFormat("ko-KR").format(n);

  // 지도: 네이버 키가 있으면 JS SDK, 없으면 OSM 정적 지도로 폴백
  const hasNaver = !!process.env.NEXT_PUBLIC_NAVER_MAP_CLIENT_ID;
  const osmImg =
    vm.lat != null && vm.lng != null
      ? `https://staticmap.openstreetmap.de/staticmap.php?center=${vm.lat},${vm.lng}&zoom=16&size=640x360&markers=${vm.lat},${vm.lng},lightblue1`
      : null;

  return (
    <main className="max-w-5xl mx-auto p-4 space-y-6">
      <header className="flex items-start justify-between gap-3">
        <div>
          <h1 className="text-xl font-semibold">{vm.title}</h1>
          {vm.address_area && (
            <div className="text-sm text-neutral-600">{vm.address_area}</div>
          )}
        </div>
        {canEdit && (
          <Link
            href={`/listing/${vm.id}/edit`}
            prefetch={false}
            className="rounded-lg border px-3 py-1.5 text-sm hover:bg-neutral-50"
          >
            수정
          </Link>
        )}
      </header>

      {/* 사진 갤러리 */}
      {vm.photos.length > 0 && (
        <section className="grid grid-cols-2 md:grid-cols-3 gap-3">
          {vm.photos.slice(0, 6).map((src, i) => (
            <div
              key={`${src}-${i}`}
              className="relative aspect-[4/3] rounded-xl overflow-hidden border bg-neutral-100"
            >
              <Image
                src={src}
                alt={`photo-${i}`}
                fill
                sizes="(max-width: 768px) 50vw, 33vw"
                style={{ objectFit: "cover" }}
              />
            </div>
          ))}
        </section>
      )}

      {/* 정보 */}
      <section className="rounded-2xl border bg-white p-4">
        <div className="grid grid-cols-2 gap-x-8 gap-y-2 text-sm">
          <div>월세: {fmt(vm.rent_monthly)}</div>
          <div>임대보증금: {fmt(vm.deposit)}</div>
          <div>권리금: {fmt(vm.goodwill_price)}</div>
          <div>월매출: {fmt(vm.monthly_sales)}</div>
          <div>주차: {vm.parking_spaces ?? 0}대</div>
          <div>위치: {vm.lat ?? "-"}, {vm.lng ?? "-"}</div>
        </div>
      </section>

      {/* 지도 (네이버 ▶ OSM 폴백) */}
      {vm.lat != null && vm.lng != null && (
        <section className="rounded-2xl border bg-white p-3">
          {hasNaver ? (
            <NaverMap
              lat={vm.lat}
              lng={vm.lng}
              markerTitle={vm.title}
              height={360}
            />
          ) : (
            <>
              {/* eslint-disable-next-line @next/next/no-img-element */}
              <img
                src={osmImg!}
                alt="위치 지도"
                className="w-full h-auto rounded-xl border"
              />
              <p className="mt-2 text-xs text-neutral-500">
                * 개발용 OSM 정적 지도(키 불필요). 환경변수 설정 시 네이버 지도로
                자동 전환됩니다.
              </p>
            </>
          )}
        </section>
      )}
    </main>
  );
}
